<template>
 	<div id="header" bool="movebool">
 		<div class="nav_btn" @click="sendMsgToParent"><a href="javascript:void(0)"></a></div>
 		<h1>XX启蒙教育</h1>
 		<div class="nav_tel"><a href="javascript:void(0)"></a></div>
 		<ul class="nav" :class="{left:message=message==null?movebool:message}"  @click.stop>
 			<li><router-link to="/"><span class="fa fa-home"></span>网站首页</a></router-link>
 			<li><router-link to="/brand"><span class="fa fa-cube"></span>xx品牌</router-link></li>
 			<li><router-link to="/course"><span class="fa fa-book"></span>课程介绍</router-link></li>
 			<li><router-link to="/activity"><span class="fa fa-location-arrow"></span>活动资讯</router-link></li>
 			<li><router-link to="/mba"><span class="fa fa-university"></span>父母MBA</router-link></li>
 			<li><router-link to="/news"><span class="fa fa-newspaper-o"></span>新闻中心</router-link></li>
 			<li><router-link to="/bespeak"><span class="fa fa-assistive-listening-systems"></span>预约试听</router-link></li>
 		</ul>
 	</div>
 	
</template>

<script>
export default {
	props:["message"],
  data () {
    return {
      movebool:false,
    }
  },
  methods:{
  	sendMsgToParent:function(){
  		this.movebool=this.movebool==false?true:false;
  		this.$emit("listenToChlidEvent",true);
  		console.log(1);
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@fz:100rem;
#header{position:relative; width: 100%;height: 81/@fz; background:#383232;};
#header h1{position:absolute;width:300/@fz;margin-left:-150/@fz;left:50%;line-height:81/@fz;font-size:32/@fz;color:#fff;}
.nav_btn{position:absolute;left:0; width: 96/@fz;height: 81/@fz;border-right: 1px #292424 solid;}
.nav_btn a{position:absolute;width:52/@fz;height: 52/@fz;background:url(../../assets/img/nav-btn.png)no-repeat;background-size:100%;left:21/@fz;top:15/@fz;}
.nav_tel{position:absolute;right:0; width: 96/@fz;height: 81/@fz;border-left: 1px #292424 solid;}
.nav_tel a{position:absolute;width:52/@fz;height: 52/@fz;background:url(../../assets/img/nav-tel.png)no-repeat;background-size:100%;left:21/@fz;top:15/@fz;}
.nav{position:fixed;top:0;left:-334/@fz; width:332/@fz;height:100%;background:#383232;transition:left 0.3s;border-right: #1e1818 1px solid;z-index: 999;};
.nav li {height: 0.98rem;line-height: 0.98rem;border-bottom: #1e1818 1px solid};
.nav a{display:block; font-size:42/@fz;line-height: 0.98rem;}
.left{left:0;}
</style>


