<template>
  <main id="app" :message="{bool}" >
  <section :class="{opacity:bool}" @click="bool=false;console" ></section>
    <section class="app_in" :class="{slate:bool}">
    <navA v-on:listenToChlidEvent="showMsgFromChlid" :message="bool"></navA>
    <router-view></router-view>
    <footera></footera>
    </section>
    
  </main>
</template>

<script lang="babel">
import navA from './components/common/nav';
import footera from './components/common/footer';
import respondFont from './assets/js/reponeFont.js';//
respondFont.init();//字体响应
export default {
    name: 'app',
    data(){
      return{
        bool:false,

      }
    },
    components:{
        navA,
        footera
    },
    methods:{
        showMsgFromChlid:function(data){
          console.log(data)
          this.bool=data;
        },
        console:function(){
          console.log(1);
        }
    } 
}

</script>

<style lang="less">
@fz:100rem;
@import "assets/css/font-awesome.min.css";
body,div,ul,li,dl,dt,dd,img,a,span,p,h1,h2,h3,h4{margin:0;padding: 0};
html{width:100%;height:100%;}
body{color: #fff;background:#f2f2f2;width:100%;height:100%;}
li{list-style: none;}
a{color: #fff;}
a{text-decoration: none;}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  min-height:100%;
 overflow:hidden;
}
.app_in{width:100%;transition:margin-left 0.3s;}
.slate{margin-left:333/@fz;}
.opacity{opacity:1;width:100%;height: 100%;position:fixed;z-index:99;}
</style>



