const store={
	mA:{
		state:{//状态//属性;组件能通过this.$store.state访问到

		},
		getters:{//获取；方法；组件能通过this.$store.getters访问到

		},
		mutations:{//变化；方法；组件能通过this.$store.commit('xxx')访问到

		},
		actions:{//动作；//方法；组件中 使用this.$store.dispatch('xxx')发分//异步操作

		}
	}
};
export default store;


