<template>
 	<div>
		<section class="news_img">
			<img src="../assets/img/2.jpg" alt="">
		</section>
		<List2 :list="listNews"></List2>
 	</div>
</template>

<script>
import List2 from './common/list2'
export default {
  
  data () {
    return {
      listNews:[],
    }
  },
  components:{
  	List2
  },
  mounted(){
  	let that=this;
  	that.$http({
  		method:"get",
  		url:"./static/json/news.json",
  	}).then(function(data){
  		that.listNews=data.data;
  	}).catch(function(err){

  	})
  

  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@fz:100rem;
.news_img{width:100%;}
.news_img img{display:block;width:100%};
</style>


