<template>
 	<div class="mba_list">
 		<dl v-for="obj in list">
 			<dt><a href=""><span></span>{{obj.name}}</a></dt>
 			<dd v-for="sonobj in obj.children"><a href="">{{sonobj.title}}</a></dd>
 		</dl>
 	</div>
</template>

<script>
export default {
  props:{
  		list:Array
  },
  data () {
    return {
      
    }
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@fz:100rem;
.mba_list {width:100%;text-align:left;};
.mba_list dt{background:#e8e8e8;line-height:82/@fz;font-size:24/@fz;color:#fff;padding-left: 80/@fz;}
.mba_list dt a{display:block;width:100%; font-size:40/@fz;color:#000;}
.mba_list dd{background:#f2f2f2;line-height:76/@fz;border-bottom:1px #e8e8e8 solid;padding-left: 80/@fz;}
.mba_list dd a{display:block;line-height:76/@fz; font-size:26/@fz;color:#3c3535;;}
</style>


