<template>
 	<div>
 		<section class="mba-img">
 			<img src="../assets/img/mba.jpg" alt="">
 		</section>
 		<List2 :list="listMba"></List2>
 	</div>
</template>

<script>
import List2 from './common/list2'
export default {
  
  data () {
    return {
      listMba:[],
    }
  },
  components:{
  	List2
  },
  mounted(){
  	let _this=this;
  	_this.$http({
  		method:"get",
  		url:"/static/json/mba.json"
  	}).then(function(data){
  		_this.listMba=data.data
  	}).catch(function(err){

  	})
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.mba-img{width:100%;}
.mba-img img{display:block;width:100%};
</style>


