<template>
 	<div>
 		<section class="brand_img">
 			<img src="../../assets/img/2.jpg" alt="">
 		</section>
 		<dl class="brand_list" v-for="obj in list">
			<dt v-if="obj.name">{{obj.name}}</dt>
 			<dd v-for="sonobj in obj.children"><router-link :to="sonobj.href"><em>{{sonobj.title}}</em><span class="fa fa-chevron-right"></span></router-link></dd>
 			
 		</dl>
