</template>

<script>

export default {
  props: {//从父得到数据
			list:Array
		},
  data () {
    return {
      brandDetail:[],
    }
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@fz:100rem;
.brand_img{width:100%;height:216/@fz;};
.brand_img img{display:block;width:100%;}
.brand_list{display:flex;height:auto;justify-content: center; flex-direction:column;padding:60/@fz 36/@fz 80/@fz 36/@fz;}
.brand_list dd{flex:1;margin:2/@fz 0;border: 1px #d4d4d4 solid;background:#fafafa;border-radius: 6/@fz; }
.brand_list dd a{display:flex;}
.brand_list dt{width: 110%;border-bottom: #00afd4 0.04rem solid; background: #ececec;line-height: 0.67rem;font-size:32/@fz;color:#3c3535;margin-top: -60/@fz; margin-bottom: 40/@fz;margin-left:-35/@fz;text-align:center;}
.brand_list dd a em{text-align:center;line-height:100/@fz; font-size:32/@fz;color:#3c3535;font-style: normal;flex:530}
.brand_list dd a span{flex:45; line-height:100/@fz;color:#999;font-size:34/@fz;}
</style>


