<template>
 	<div>
		<List :list="listBrand" class="min-height"></List>
 	</div>
</template>

<script>
import List from './common/list';
export default {
  
  data () {
    return {
      listBrand:[],
    }
  },
  components:{
        List
    },
    mounted(){
			let _this=this;
			_this.$http({
				method:"get",
				url:"/static/json/brand.json",
				params:{},
				
			}).then(function(data){
				console.log(data,data.data)
				_this.listBrand=data.data
				console.log(typeof _this.listBrand)

			}).catch(function(err){

			});
		}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@fz:100rem;

.min-height{min-height:1130/@fz;}
</style>


