<template>
<div>
  <section class="slider" @touchstart="imgStart" @touchmove="imgMove" @touchend="imgEnd">
      <ul class="slider_img" :class="{slider_flash:flashBool}" :style="ulX">
        <li><a href="javascript:void(0)"><img src="../assets/img/1.jpg" alt=""></a></li>
        <li><a href="javascript:void(0)"><img src="../assets/img/1.jpg" alt=""></a></li>
        <li><a href="javascript:void(0)"><img src="../assets/img/1.jpg" alt=""></a></li>
        <li><a href="javascript:void(0)"><img src="../assets/img/1.jpg" alt=""></a></li>
      </ul>
      <div class="slider_btn">
          <span class="current"></span>
          <span></span>
          <span></span>
          <span></span>
      </div>
  </section>
  <section class="List">
      <ul class="list">
          <li style="background:#0bbebb;"><router-link to="/brand" ><span class="fa fa-cube"></span>xx品牌</router-link></li>
          <li style="background:#afd23a;"><router-link to="/course"><span class="fa fa-book"></span>课程介绍</router-link></li>
          <li style="background:#f48221;"><router-link to="/activity"><span class="fa fa-location-arrow"></span>活动资讯</router-link></li>
      </ul>
      <ul class="list">
          <li style="background:#de0000;"><router-link to="/mba"><span class="fa fa-university"></span>父母MBA</router-link></li>
          <li style="background:#3eb7ee;"><router-link to="/news"><span class="fa fa-newspaper-o"></span>新闻中心</router-link></li>
          <li style="background:#6eb943;"><a href="javascript:void(0)"><span class="fa fa-assistive-listening-systems"></span>预约试听</a></li>
      </ul>
  </section>
</div>
</template>

<script>

export default {

  data () {
    return {
      flashBool:false,
      startX:null,
      moveX:null,
      gomoveX:null,
      endX:null,
      ulX:null,
      stopnum:0,
      ulWidth:750,
      spanNodes:null,
      num:null
    }
  },
  methods:{
    imgStart:function(e){
        let that=this;
      that.startX=e.targetTouches[0].pageX;
      that.flashBool=false;
    },
    imgMove:function(e){
        let that=this;
        that.flashBool=false;
      that.moveX=e.targetTouches[0].pageX;
      that.gomoveX=-that.stopnum+(that.moveX-that.startX)*2
      
      that.ulX="left:"+that.gomoveX/100+"rem";

      console.log(that.gomoveX)

    },
    imgEnd:function(e){
        let that=this;
        let X;
        that.flashBool=true;
      that.endX=e.changedTouches[0].pageX;
      that.spanNodes=$(".slider_btn span");
      X=that.endX-that.startX;
      that.num=$(".current").index();
      
      if(Math.abs(X)<that.ulWidth/4){
          that.ulX="left:"+-that.ulWidth*that.num/100+"rem";
          console.log(that.num)
      }
      else if(X<0&&that.num<3){
          that.stopnum=that.ulWidth*(that.num+1)
          that.ulX="left:"+-that.stopnum/100+"rem";
            that.moveX=null;
            that.startX=null;
            that.gomoveX=null;
            that.spanNodes.eq(that.num).removeClass("current");
            that.num++;
            that.spanNodes.eq(that.num).addClass("current");
      }
      else if(X<0&&that.num==3){
              that.ulX="left:"+-3*that.ulWidth/100+"rem";
              that.stopnum=3*that.ulWidth;
              that.moveX=null;
            that.startX=null;
            that.gomoveX=null;
            }

      else if(X>0&&that.num>0){
          that.stopnum=that.ulWidth*(that.num-1)
          that.ulX="left:"+-that.stopnum/100+"rem";
            
            that.moveX=null;
            that.startX=null;
            that.gomoveX=null;
            that.spanNodes.eq(that.num).removeClass("current");
            that.num--;
            that.spanNodes.eq(that.num).addClass("current");
      }
      else if(X>0&&that.num==0){

              that.ulX="left:"+0+"rem";

              that.stopnum=0;
              that.moveX=null;
            that.startX=null;
            that.gomoveX=null;
            }
    }

  },
  components:{
     
  } 
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@fz:100rem;
.slider{position:relative; width:100%;height:430/@fz;overflow:hidden;z-index:1}
.slider_img{width:400%;height:100%;display: flex;position: relative;}
.slider_flash{transition:left 0.3s}
.slider_img li{flex:1;}
.slider_img li img{width:100%;}
.slider_btn{position: absolute;left: 0;bottom: 0; width: 100%;height: 44/@fz;padding-top:14/@fz;text-align: center;background: rgba(241, 242, 242, 0.6);display: flex;justify-content: center;}
.slider_btn span{display:inline-block;width:20/@fz;height:20/@fz;background:#fff;border-radius:50%;margin-left:6/@fz;}
.slider_btn .current{background: #6a6060;}
.List{min-height:700/@fz;}
.list{display:flex;width:100%;}
.list li{width:206/@fz;height:142/@fz;margin:10/@fz 22/@fz; background:#f0f0f0;font-size:24/@fz;padding-top:30/@fz;}
.list li span{width:100%;text-align:center;font-size:75/@fz;padding-bottom:10/@fz;}
.list li a{display:inline-block;width:100%;height:100%}
</style>



