<template>
 	<div>
 		<section class="bespeak">
 			<section class="bespeak-in">
 				<h2>课程试听预约</h2>
 				<p>免费名额有限，现在就留下您的电话号码，我们的顾问会尽
快联系您，并为您预约一个合适的时间。</p>
				<section class="bespeak_list">
					<section class="input">
						<label>宝宝姓名：</label>
						<input type="text" placeholder="请输入宝宝姓名">
					</section>
					<section class="input">
						<label>宝宝年龄:</label>
						<input type="text" placeholder="选择日期" @click="ageList='display:block';bgShow=true">
					</section>
					<section class="input">
						<label>联系电话:</label>
						<input type="text" placeholder="请输入手机号">
					</section>
				</section>
				<button class="bespeak_btn">点击确定</button>
 			</section>
 		</section>
 		<section class="ageList" :style="ageList">
 			<span class="Xbtn" @click="ageList='display:none';bgShow=false;ulY=null">×</span>
 			<span class="Vbtn" @click="ageList='display:none';bgShow=false">√</span>
 			<section class="ageList-in">
 				<div @touchstart="ulStart" @touchmove="ulMove" @touchend="ulEnd">
 					<ul :style="ulY">
 						<li v-for="obj in yearData">{{obj.into}}</li>
 					</ul>
 				</div>
 				<div @touchstart="ulStartSecond" @touchmove="ulMoveSecond" @touchend="ulEndSecond">
 					<ul :style="ulYsceond">
 						<li v-for="obj in mouthData">{{obj.into}}</li>
 					</ul>
 				</div>
 				<div @touchstart="ulStartThird" @touchmove="ulMoveThird" @touchend="ulEndThird">
 					<ul :style="ulYThird">
 						<li v-for="obj in mouthData">{{obj.into}}</li>
 					</ul>
 				</div>

 			</section>
 		</section>
 		<section :class="{bg:bgShow}"></section>
 	</div>
</template>

<script>
export default {
  methods:{
  	ulStart:function(e){
  		let that=this;
  		that.startY=e.changedTouches[0].pageY;
  		
  	},
  	ulMove:function(e){
  		let that=this;
  		that.moveY=e.changedTouches[0].pageY;
  		that.gomoveY=that.gomoveY+that.moveY-that.startY
  		
  		that.ulY="margin-top:"+that.gomoveY/45+"px";
  		
  	},
  	ulEnd:function(){
  		let that=this;
  		that.startY=null;
  		that.moveY=null;
  	},
  	ulStartSecond:function(e){
  		let that=this;
  		that.startY=e.changedTouches[0].pageY;
  		
  	},
  	ulMoveSecond:function(e){
  		let that=this;
  		that.moveY=e.targetTouches[0].pageY;
  		that.gomoveY=that.gomoveY+that.moveY-that.startY
  		
  		that.ulYsceond="margin-top:"+that.gomoveY/45+"px";
  		
  	},
  	ulEndSecond:function(e){
  		let that=this;
  		that.startY=null;
  		that.moveY=null;
  	},
  	ulStartThird:function(e){
  		let that=this;
  		that.startY=e.targetTouches[0].pageY;
  		
  	},
  	ulMoveThird:function(e){
  		let that=this;
  		that.moveY=e.targetTouches[0].pageY;
  		that.gomoveY=that.gomoveY+that.moveY-that.startY
  		
  		that.ulYThird="margin-top:"+that.gomoveY/45+"px";
  		
  	},
  	ulEndThird:function(e){
  		let that=this;
  		that.startY=null;
  		that.moveY=null;
  	}
  },
  data () {
    return {
    	startY:null,
    	moveY:null,
    	gomoveY:null,
    	ulY:null,
    	ulYsceond:null,
    	ulYThird:null,
      ageList:"display:none",
      bgShow:false,
      yearData:[
      				{into:1990},
      				{into:1991},
					{into:1992},
      				{into:1993},
      				{into:1994},
      				{into:1995},
      				{into:1996},
      				{into:1997},
      				{into:1998},
      				{into:1999},
      				{into:2000},
      				{into:2001},
      				{into:2002},
      				{into:2003},
      				{into:2004},
      				{into:2005},
      				{into:2006},
      				{into:2007},
      				{into:2008},
      				{into:2009},
      				{into:2010},
      				{into:2011},
      				{into:2012},
      				{into:2013},
      				{into:2014},
      				{into:2015},
      				{into:2016},
      				{into:2017},
      		],

      		
      mouthData:[

      				{into:1},
      				{into:2},
      				{into:3},
      				{into:4},
      				{into:5},
      				{into:6},
      				{into:7},
      				{into:8},
      				{into:9},
      				{into:10},
      				{into:11},
      				{into:12},

      			],
      		
      dayData:[
      				{into:1},
      				{into:2},
      				{into:3},
      				{into:4},
      				{into:5},
      				{into:6},
      				{into:7},
      				{into:8},
      				{into:9},
      				{into:10},
      				{into:11},
      				{into:12},
      				{into:13},
      				{into:14},
      				{into:15},
      				{into:16},
      				{into:17},
      				{into:18},
      				{into:19},
      				{into:20},
      				{into:21},
      				{into:22},
      				{into:23},
      				{into:24},
      				{into:25},
      				{into:26},
      				{into:27},
      				{into:28},
      				{into:29},
      				{into:30},
      				{into:31}
      			]
      		
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@fz:100rem;
input{border:none;}
.bespeak{min-height:1100/@fz;text-align:left;font-size: 24/@fz;}
.bespeak-in{width:92%;height:672/@fz;border-bottom:8/@fz #433c3c solid;margin: 0 auto;background:#fff;margin-top: 30/@fz;}
.bespeak-in h2{width:100%;height:120/@fz;background:#443b3c;color:#fff;font-size:36/@fz;line-height:120/@fz;text-indent: 1em;}
.bespeak-in p{margin:14/@fz;background:#f0f0f0;color:#666;line-height:33/@fz;padding:20/@fz 36/@fz;font-size: 24/@fz;border-radius:6/@fz;}
.bespeak_list{width:84%;margin:22/@fz 0 96/@fz 46/@fz;}
.input{display: flex;margin-bottom: 0.12rem}
.input input{padding: 0.06rem;border: #d8d8d8 1px solid;line-height: 0.4rem;flex:3;}
.input label{flex:1;line-height: 58/@fz;}
.bespeak_btn{display: block;margin: 0 auto;width: 83%;line-height: 0.62rem;border: 0 none;background: #db3129;color: #fff;border-radius: 0.04rem;
}
.ageList{height:560/@fz;width:100%;position:fixed;bottom:0;left:0;background:#fff;z-index:99}
.ageList span{width:94/@fz;height:94/@fz;position:absolute;top:0;font-family: Georgia;line-height: 94/@fz;font-size: 60/@fz;}
.ageList .Xbtn{left:0}
.ageList .Vbtn{right:0}
.bg{width: 100%;height: 100%;position: fixed;top: 0;background-color: rgba(0, 0, 0, 0.4);left:0;z-index:1;}
.ageList-in{position:absolute;bottom:0; display:flex;width:100%;height:466/@fz;}
.ageList-in div{flex:1;overflow:hidden;}
.ageList-in div ul{height:10000%;width:100%;transition:margin-top 0.15s;}
.ageList-in div ul li{line-height:466/@fz/5;font-size:34/@fz;color:#453d3d;;font-family: "\5FAE\8F6F\96C5\9ED1", Microsoft YaHei;}
</style>


